import { Button } from '@/components/ui/button'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { PlayCircle, Repeat } from 'lucide-react'
import Image from 'next/image'
import { GameState } from '@/app/page'

type ControlPanelProps = {
  gameState: GameState;
  onPlay: () => void;
  betAmount: number;
  onBetAmountChange: (amount: number) => void;
};

export default function ControlPanel({ gameState, onPlay, betAmount, onBetAmountChange }: ControlPanelProps) {
  const betAmounts = [100, 500, 1000, 2000, 5000];
  const isRunning = gameState === 'running';

  return (
    <div className="absolute bottom-0 left-0 right-0 z-20 p-4">
      <div className="max-w-4xl mx-auto bg-black/40 backdrop-blur-md rounded-2xl p-2 flex items-center justify-between gap-4 border border-white/10 shadow-2xl">
        <div className="flex items-center gap-2">
          <p className="text-white font-semibold mr-2 hidden sm:block">Bet:</p>
          {betAmounts.map(amount => (
            <Button 
              key={amount} 
              variant={betAmount === amount ? "primary" : "secondary"} 
              className="font-bold"
              onClick={() => onBetAmountChange(amount)}
              disabled={isRunning}
            >
              ₹{amount}
            </Button>
          ))}
        </div>
        
        <div className="flex items-center gap-2">
          <Select defaultValue="easy" disabled={isRunning}>
            <SelectTrigger className="w-[120px] sm:w-[180px] bg-secondary text-secondary-foreground font-bold">
              <SelectValue placeholder="Difficulty" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="easy">Easy</SelectItem>
              <SelectItem value="medium">Medium</SelectItem>
              <SelectItem value="hard">Hard</SelectItem>
            </SelectContent>
          </Select>
          
          <Button 
            size="lg" 
            className="bg-primary hover:bg-primary/90 text-primary-foreground font-bold text-lg sm:text-xl px-4 sm:px-8 py-6 rounded-xl shadow-lg hover:scale-105 transition-transform relative"
            onClick={onPlay}
            disabled={isRunning}
          >
            <Image src="https://chickenroad.rajmines.com/images/play_btn_bg.png" alt="Play" layout="fill" className="rounded-xl" />
            <div className="relative z-10 flex items-center">
              {gameState === 'finished' ? <Repeat className="mr-2 h-6 w-6" /> : <PlayCircle className="mr-2 h-6 w-6" />}
              {gameState === 'finished' ? 'Play Again' : 'Play'}
            </div>
          </Button>
        </div>
      </div>
    </div>
  )
}
