'use client';
import { useState } from 'react';
import Link from 'next/link';
import { Menu, Wallet, LogIn, Banknote, Landmark } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { DepositModal } from '@/components/modals/DepositModal';
import { WithdrawModal } from '@/components/modals/WithdrawModal';

type GameHeaderProps = {
  onMenuClick: () => void;
};

export default function GameHeader({ onMenuClick }: GameHeaderProps) {
  const [activeModal, setActiveModal] = useState<string | null>(null);

  return (
    <>
      <header className="absolute top-0 left-0 right-0 z-20 flex items-center justify-between p-4 bg-transparent">
        <div className="flex items-center gap-2">
            <Button variant="ghost" size="icon" onClick={onMenuClick} aria-label="Open Menu">
                <Menu className="h-6 w-6 text-white" />
            </Button>
             <span className="text-xs font-bold text-white bg-black/30 px-2 py-1 rounded-md hidden sm:inline-block">v1.0</span>
        </div>
       
        <h1 className="text-2xl font-bold text-accent font-headline tracking-wider hidden sm:block" style={{textShadow: '2px 2px 4px rgba(0,0,0,0.5)'}}>
            Chicken Road Riches
        </h1>

        <div className="flex items-center gap-2 sm:gap-4">
            <div className="hidden sm:flex items-center gap-2">
                <Button variant="secondary" onClick={() => setActiveModal('deposit')}>
                    <Banknote />
                    Deposit
                </Button>
                <Button variant="secondary" onClick={() => setActiveModal('withdraw')}>
                    <Landmark />
                    Withdraw
                </Button>
            </div>
            <div className="flex items-center gap-2 rounded-full bg-black/30 p-2 text-white shadow-lg">
                <Wallet className="h-6 w-6 text-primary" />
                <span className="font-bold text-lg">₹300.00</span>
            </div>
            <Link href="/admin">
            <Button variant="ghost" size="icon" aria-label="Admin Login">
                <LogIn className="h-5 w-5 text-white" />
            </Button>
            </Link>
        </div>
      </header>

      <DepositModal isOpen={activeModal === 'deposit'} onOpenChange={(open) => !open && setActiveModal(null)} />
      <WithdrawModal isOpen={activeModal === 'withdraw'} onOpenChange={(open) => !open && setActiveModal(null)} />
    </>
  );
}
