'use client';

import Image from 'next/image';
import { useMemo, useEffect, useRef } from 'react';
import { GameState } from '@/app/page';

const Gate = ({ multiplier, position, delay }: { multiplier: string, position: string, delay: string }) => (
  <div
    className="absolute flex items-center justify-center transition-all duration-500"
    style={{ 
      top: position, 
      animation: `gate-move 10s linear ${delay} infinite`,
    }}
  >
    <div className="relative flex items-center justify-center h-20 w-20">
       <Image src="https://chickenroad.rajmines.com/images/gate.png" alt="Gate" width={80} height={80} unoptimized />
      <span className="absolute text-xl font-bold text-white drop-shadow-lg" style={{textShadow: '2px 2px 4px rgba(0,0,0,0.8)'}}>{multiplier}x</span>
    </div>
  </div>
);

const GatesContainer = () => {
    const gates = useMemo(() => [
        { multiplier: '1.0', position: '20%', delay: '0s' },
        { multiplier: '1.5', position: '50%', delay: '0s' },
        { multiplier: '2.0', position: '80%', delay: '0s' },
        { multiplier: '2.5', position: '35%', delay: '5s' },
        { multiplier: '3.0', position: '65%', delay: '5s' },
    ], []);

    return (
        <>
            <style jsx>{`
                @keyframes gate-move {
                    0% {
                        left: 110%;
                        opacity: 0;
                    }
                    10% {
                        opacity: 1;
                    }
                    90% {
                        opacity: 1;
                    }
                    100% {
                        left: -10%;
                        opacity: 0;
                    }
                }
            `}</style>
            <div className="absolute inset-0 overflow-hidden">
                {gates.map((gate, i) => (
                    <Gate key={i} {...gate} />
                ))}
            </div>
        </>
    );
};


type GameSceneProps = {
  gameState: GameState;
  onGameEnd: () => void;
};

export default function GameScene({ gameState, onGameEnd }: GameSceneProps) {
  const chickenRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (gameState === 'running' && chickenRef.current) {
      const animation = chickenRef.current.animate(
        [
          { left: '25%', offset: 0 },
          { left: '110%', offset: 1 },
        ],
        {
          duration: 4000, // 4 seconds to cross the screen
          easing: 'linear',
          fill: 'forwards',
        }
      );
      animation.onfinish = () => {
        onGameEnd();
      };
    } else if ((gameState === 'ready' || gameState === 'finished') && chickenRef.current) {
        // Reset chicken position
        chickenRef.current.style.left = '25%';
    }
  }, [gameState, onGameEnd]);

  return (
    <div className="w-full h-full relative scroll-road">
      <div 
        ref={chickenRef} 
        className={`absolute z-10 top-1/2 -translate-y-1/2 ${gameState !== 'running' ? 'chicken-bounce' : ''}`}
        style={{ left: '25%' }} // Initial position
      >
        <Image
          src="https://chickenroad.rajmines.com/images/chicken.png"
          alt="Animated Chicken"
          width={120}
          height={120}
          unoptimized
          className="drop-shadow-2xl"
        />
      </div>
      {gameState !== 'ready' && <GatesContainer />}
    </div>
  );
}
