'use client';

import { useState } from 'react';
import GameHeader from '@/components/game/GameHeader';
import PlayerProfile from '@/components/game/PlayerProfile';
import GameScene from '@/components/game/GameScene';
import ControlPanel from '@/components/game/ControlPanel';
import { Sidebar } from '@/components/game/Sidebar';

export type GameState = 'ready' | 'running' | 'finished';

export default function Home() {
  const [isSidebarOpen, setSidebarOpen] = useState(false);
  const [gameState, setGameState] = useState<GameState>('ready');
  const [betAmount, setBetAmount] = useState<number>(100);

  const handlePlay = () => {
    if (gameState === 'ready' || gameState === 'finished') {
      setGameState('running');
    }
  };

  const handleGameEnd = () => {
    setGameState('finished');
    // Here you could add logic for winnings, etc.
  }

  return (
    <div className="relative flex h-screen w-full flex-col overflow-hidden bg-background font-body">
      <GameHeader onMenuClick={() => setSidebarOpen(true)} />
      <div className="relative flex-1">
        <GameScene gameState={gameState} onGameEnd={handleGameEnd} />
        <PlayerProfile />
      </div>
      <ControlPanel 
        gameState={gameState} 
        onPlay={handlePlay} 
        betAmount={betAmount}
        onBetAmountChange={setBetAmount}
      />
      <Sidebar isOpen={isSidebarOpen} onOpenChange={setSidebarOpen} />
    </div>
  );
}
