'use client'
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Info } from "lucide-react"
import { useState } from "react"

type ModalProps = {
  isOpen: boolean;
  onOpenChange: (isOpen: boolean) => void;
};

export function WithdrawModal({ isOpen, onOpenChange }: ModalProps) {
  const [amount, setAmount] = useState(0);
  const processingFee = 0.10; // 10%
  const receivedAmount = amount - (amount * processingFee);

  return (
    <Dialog open={isOpen} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-md bg-background border-primary/50">
        <DialogHeader>
          <DialogTitle className="text-2xl text-primary">Request Withdrawal</DialogTitle>
          <DialogDescription>
            Enter the amount you wish to withdraw from your wallet.
          </DialogDescription>
        </DialogHeader>
        <div className="space-y-4">
            <Alert className="border-accent text-accent-foreground [&>svg]:text-accent">
                <Info className="h-4 w-4" />
                <AlertTitle>Please Note</AlertTitle>
                <AlertDescription>
                    Withdrawals are subject to admin approval. A 10% processing fee will be applied. You can only make one withdrawal every 24 hours.
                </AlertDescription>
            </Alert>
            <div className="space-y-2">
                <Label htmlFor="withdraw-amount">Amount</Label>
                <Input id="withdraw-amount" placeholder="Enter amount" type="number" onChange={(e) => setAmount(Number(e.target.value))} />
            </div>
            {amount > 0 && (
                 <div className="p-3 bg-secondary rounded-md text-sm">
                    <div className="flex justify-between"><span>Processing Fee (10%):</span> <span className="font-medium">- ₹{ (amount * processingFee).toFixed(2) }</span></div>
                    <div className="flex justify-between font-bold mt-2 pt-2 border-t"><span>You will receive:</span> <span>₹{ receivedAmount.toFixed(2) }</span></div>
                </div>
            )}
             <div className="space-y-2">
                <Label htmlFor="withdraw-method">UPI / Bank Account Number</Label>
                <Input id="withdraw-method" placeholder="Enter your withdrawal details" />
            </div>
        </div>
        <DialogFooter>
          <Button type="submit" className="w-full h-12 bg-primary hover:bg-primary/90 text-primary-foreground font-bold text-lg">
            Request Withdrawal
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
