'use client';
import { useState } from 'react';
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetDescription,
  SheetFooter
} from "@/components/ui/sheet"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"
import { Separator } from "@/components/ui/separator"
import { Switch } from "@/components/ui/switch"
import { 
  Music, 
  Volume2, 
  Banknote, 
  Landmark, 
  History, 
  ScrollText, 
  UserPlus, 
  ShieldCheck, 
  FileQuestion, 
  LifeBuoy, 
  LogOut,
  ArrowRightLeft,
  LogIn
} from 'lucide-react'
import { DepositModal } from '@/components/modals/DepositModal'
import { WithdrawModal } from '@/components/modals/WithdrawModal'
import { InfoModal } from '@/components/modals/InfoModal';
import Link from 'next/link';

type SidebarProps = {
  isOpen: boolean;
  onOpenChange: (isOpen: boolean) => void;
};

export function Sidebar({ isOpen, onOpenChange }: SidebarProps) {
  const [activeModal, setActiveModal] = useState<string | null>(null);

  const menuItems = [
    { icon: Banknote, label: 'Deposit', modal: 'deposit', mobileOnly: true },
    { icon: Landmark, label: 'Withdraw', modal: 'withdraw', mobileOnly: true },
    { icon: ArrowRightLeft, label: 'Transaction History', modal: 'transaction_history' },
    { icon: History, label: 'Bet History', modal: 'bet_history' },
    { icon: UserPlus, label: 'Refer Code', modal: 'refer_code' },
    { icon: ShieldCheck, label: 'KYC', modal: 'kyc' },
  ];

  const infoItems = [
    { icon: FileQuestion, label: 'Game Rules', modal: 'rules' },
    { icon: ScrollText, label: 'How to Play', modal: 'how_to_play' },
    { icon: LifeBuoy, label: 'Support', modal: 'support' },
  ];

  const handleMenuClick = (modal: string) => {
    onOpenChange(false);
    setTimeout(() => setActiveModal(modal), 200);
  }

  return (
    <>
      <Sheet open={isOpen} onOpenChange={onOpenChange}>
        <SheetContent className="bg-background/80 backdrop-blur-lg border-l-primary/30 text-foreground p-0 w-[320px] sm:w-[400px]">
          <div className="flex flex-col h-full">
            <SheetHeader className="p-6 text-left">
              <div className="flex items-center gap-4">
                <Avatar className="h-16 w-16 border-4 border-primary">
                  <AvatarImage src="https://placehold.co/100x100.png" alt="Ishaan" data-ai-hint="player avatar" />
                  <AvatarFallback>I</AvatarFallback>
                </Avatar>
                <div>
                  <SheetTitle className="text-xl">Ishaan</SheetTitle>
                  <SheetDescription>sonickuwal@gmail.com</SheetDescription>
                  <Button variant="link" className="p-0 h-auto text-accent">Change Avatar</Button>
                </div>
              </div>
            </SheetHeader>
            <div className="px-6 space-y-4">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <Music className="h-5 w-5" />
                  <span>Music</span>
                </div>
                <Switch defaultChecked />
              </div>
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <Volume2 className="h-5 w-5" />
                  <span>Sound</span>
                </div>
                <Switch defaultChecked />
              </div>
            </div>
            <Separator className="my-4" />
            <div className="flex-1 overflow-y-auto px-6">
              <nav className="flex flex-col gap-2">
                {menuItems.map(item => (
                  <Button 
                    key={item.label} 
                    variant="ghost" 
                    className={`justify-start gap-3 text-base h-12 ${item.mobileOnly ? 'sm:hidden' : ''}`} 
                    onClick={() => handleMenuClick(item.modal)}>
                    <item.icon className="h-5 w-5 text-primary" />
                    {item.label}
                  </Button>
                ))}
              </nav>
              <Separator className="my-4" />
              <nav className="flex flex-col gap-2">
                {infoItems.map(item => (
                  <Button key={item.label} variant="ghost" className="justify-start gap-3 text-base h-12" onClick={() => handleMenuClick(item.modal)}>
                    <item.icon className="h-5 w-5 text-muted-foreground" />
                    {item.label}
                  </Button>
                ))}
                 <Link href="/admin" passHref>
                  <Button variant="ghost" className="justify-start gap-3 text-base h-12 w-full">
                    <LogIn className="h-5 w-5 text-muted-foreground" />
                    Admin Login
                  </Button>
                </Link>
              </nav>
            </div>
            <SheetFooter className="p-6 flex-col !space-x-0 gap-4">
               <Button variant="destructive" className="w-full h-12 text-base">
                <LogOut className="mr-2 h-5 w-5"/>
                Logout
              </Button>
              <p className="text-center text-xs text-muted-foreground">Powered by <span className="text-accent font-bold">yaar tera badmas hai</span> <span className="text-white">jaanu</span></p>
            </SheetFooter>
          </div>
        </SheetContent>
      </Sheet>
      
      <DepositModal isOpen={activeModal === 'deposit'} onOpenChange={(open) => !open && setActiveModal(null)} />
      <WithdrawModal isOpen={activeModal === 'withdraw'} onOpenChange={(open) => !open && setActiveModal(null)} />

      <InfoModal 
        isOpen={['transaction_history', 'bet_history', 'refer_code', 'kyc', 'rules', 'how_to_play', 'support'].includes(activeModal ?? '')} 
        onOpenChange={(open) => !open && setActiveModal(null)}
        title={activeModal?.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase()) ?? ''}
        >
        <p>Content for {activeModal} goes here.</p>
        {activeModal === 'kyc' && 
          <div className="mt-4 space-y-4">
             <p>Please complete your KYC verification.</p>
             {/* KYC form can be added here */}
          </div>
        }
      </InfoModal>
    </>
  );
}
